import React from "react";
import "./Hero.css";
import Header from "../Header/Header";
import Heart from "../../img/heartLogo.png"
import hero_image from "../../img/img3.png"
import cal from "../../img/CaloriesChart.webp"
import NumberCounter from 'number-counter';

const Hero = () => {
 
  return (
    
    <div className="hero">
      <div className="blur blur-h"></div>
      <div className="left-h container">
        <Header />
        {/* the best ad */}
        <div className="the-best-ad">
          <div ></div>
          <span>the best fitness club in the town</span>
        </div>

        {/* Hero Heading */}
        <div className="hero-text">
          <div id="div-1">
            <span className="stroke-text " id="shape">Shape </span>
            <span >Your</span>
          </div>
          <div id="div-2">
            <span>Ideal Body</span>
          </div>
          <div id="div-3">
            <span>
              Lorem ipsum dolor sit, amet consectetur adipisicing elit. Nam tempore cumque maiores provident accusantium perferendis 
            </span>
          </div>
        </div>
        
        {/* figures */}

        <div className="figures">
          <div>
            <span>
              <NumberCounter end={140} start = {1} delay = '3' preFix= "+"/>
            </span>
            <span>Expert Coachs</span>
          </div>
          <div>
            <span>
              <NumberCounter end={978} start = {1} delay = '1' preFix= "+"/>
            </span>
            <span>Members Joined</span>
          </div>
          <div>
            <span>
            <NumberCounter end={50} start = {1} delay = '4' preFix= "+"/>
            </span>
            <span>Fitness Program</span>
          </div>
        </div>

        {/* Hero Buttons */}
        <div className="hero-buttons">
          <button className="btm">Get Started</button>
          <button className="btm">Learn More</button>
        </div>
      </div>


      <div className="right-h">
        {/* <a href="#join-us"><button className="btm" >Join Now</button></a> */}

        <div className="heart-rate" >
          <img src={Heart} alt="" />
          <span>Heart Rate</span>
          <span>116 bpm</span>
        </div>

        {/* hero image */}
        <img src={hero_image} alt="" className="hero-image" />
        {/* calories */}
        <div className="calories">
          <img src={cal} alt="" />
          <div>
            <span >Calories Burned</span>
            <span>220 kcal</span>
          </div>
        </div>
      </div>
    </div>
    
  );
};

export default Hero;
