import React from 'react'
import './Header.css'
import Logo from '../../img/logo.avif'

const Header = () => {
  return (

    <div className='header'>
      <img src={Logo} alt="" className='logo'/>
      <nav className="navbar navbar-expand-lg bg-#3c3f45 " style={{left: "-2rem",width: "48rem"}}>
  <div className="container-fluid">
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0  " style={{left: "6rem",position: "relative", gap: "2rem",fontSize: "20px"}}>
        <li className="nav-item c-orange">
          <a className="nav-link active" aria-current="page" href="/" style={{color: "orange"}}>Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#programs" style={{color: "orange"}}>Programs</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#Reasons" style={{color: "orange"}}>Why us</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#plans" style={{color: "orange"}}>Plans</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#testimonials" style={{color: "orange"}}>Testimonals</a>
        </li>
      </ul>
    </div>
  </div>
   </nav>
    </div>
    
  )
}

export default Header
