import React from 'react';
import './Footer.css';
import fc from "../../img/facebook.webp"
import ins from "../../img/instagram.png"
import git from "../../img/github.png"

const Footer = () => {
  return (
    <footer className="mt-3 py-5" id='footer'>
         
        <div className="container">
            <div className="footer-logo text-center">
                {/* <img src="img/img3.1.jpg" style="max-width: 100%; height: 68px;" className="service-box"/> */}
            </div>
            <div className="footer-social d-flex justify-content-center gap-3">
                <a href="https://www.facebook.com/" ><img style={{height: "4rem",width: "4rem",top:"-7px",position:"relative"}} src={fc} alt="" /></a>
                <a href="https://www.instagram.com/" className="text-white my-2 "><img src={ins} alt="" /></a>
                <a href="https://www.github.com/" className="text-white my-2" style={{ position: "relative",left: "1rem"}}><img src={git} alt="" /></a>
                
            </div>
        </div>
        
        <div className="footer-copyright text-center mt-3 mt-lg-2">
            <p style={{
                    color: "white",
                    fontFamily: "italic",
                    fontSize: "20px"
            }}>Copyright 2023 Jul | Powered by Surjo Services</p>
        </div>
        <div className="blur f-blur-1"></div>
        <div className="blur f-blur-2"></div>
       
    </footer>
  )
}

export default Footer
