import React from 'react'
import './Program.css'
import cardd from "../../img/card1.webp"
import cd from "../../img/card2.png"
import ca from "../../img/card3.png"
import cr from "../../img/card4.webp"



function Program() {
  return (
    <div className="programs" id="programs">
        {/* header */}
        <div className="programs-header">
            <span className='stroke-text'>Explore Our</span>
            <span id='sc'>Programs</span>
            <span className='stroke-text'>to shape you</span>
        </div>

     <div className="program-categories row">
            
        <div className="card col-md-3"  style={{width: "18rem", height: "21rem"}}>
            <img src={cardd} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">Strength Training</h5>
                    <p className="card-text">In this program,You are trained to improve your strength through many excercies </p>
                    <a href="/" className="btn btn-primary">Join Now</a>
                </div>
        </div>
        <div className="card col-md-3" style={{width: "18rem" , height: "21rem"}}>
            <img src={cd} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">Health Fitness</h5>
                    <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                    <a href="/" className="btn btn-primary">Join Now</a>
                </div>
        </div>
        <div className="card" style={{width: "18rem" , height: "21rem"}}>
            <img src={ca} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">Cardio Training</h5>
                    <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                    <a href="/" className="btn btn-primary">Join Now</a>
                </div>
        </div>
        <div className="card" style={{width: "18rem",height: "21rem"}}>
            <img src={cr} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">Fat Burning</h5>
                    <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                    <a href="/" className="btn btn-primary">Join Now</a>
                </div>
        </div>

      </div>
    </div>
  )
}

export default Program
