import React from 'react'
import "./Join.css"
const Join = () => {
  return (
    <div className="join" id="join-us">
      <div className="left-j">
        <hr />
        <div id='div1'>
          <span className='stroke-text' id='re'>READY TO</span>
          <span id='level'>LEVEL UP</span>
        </div>
        <div>
          <span id='j'>YOUR BODY</span>
          <span className='stroke-text' id='wh'>WITH US?</span>
        </div>
      </div>

      <div className="right-j">
        <form action="" className='email-container'id='join'>
          <input type="email" name="User_email" placeholder='Enter Your Email Address' />
          <button className='btm btm-j'>Join Now</button>
        </form>
      </div>
    </div>
  )
}

export default Join
